import { Component, OnInit } from '@angular/core';
import { Mocks } from '../../../testing/utilities/EnsureNamespaces';
import IDropDownMenuItem from '../../shared/enums/drop-down/drop-down-menu-item';

@Component({
    selector: 'k2-filter-control-harness',
    templateUrl: './filter-control-harness.component.html',
    styleUrls: ['./filter-control-harness.component.less']
})
export class FilterControlHarnessComponent implements OnInit {

    public configuredFilter: any;
    public properties: IDropDownMenuItem[];
    public initialStateDescription: string;

    constructor() {
        Mocks.ensureNamespace('sourceCode.k2Designer.objectModel.core', null);
        Mocks.ensureNamespace('sourceCode.k2Designer.objectModel.core.configuredRules', null);
        Mocks.ensureNamespace('sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredComplexLogicalGroupExpression', {
            implementor: ''
        });

        this.configuredFilter = {
            expressions: [
                {
                    instanceOf: () => false
                }
            ]
        };
        this.configuredFilter.expressions.listen = () => { };
        this.properties = [
            {
                title: 'Browse',
                key: 'browse',
                icon: 'icon-set-toolbox ic-category'
            }
        ];
        this.initialStateDescription = 'Harness Filter';
    }

    ngOnInit(): void {
    }

}
