import { Component, OnInit } from '@angular/core';
import { ISelectableTile } from 'src/types/models/selectable-tile/i-selectable-tile';

@Component({
    selector: 'k2-tile-select-harness',
    templateUrl: './tile-select-harness.component.html',
    styleUrls: ['./tile-select-harness.component.less']
})
export class TileSelectHarnessComponent implements OnInit {

    tiles: ISelectableTile[] = [];

    changeMessage = '';

    constructor() { }

    ngOnInit(): void {
        for (let i = 0; i < 10; i++) {
            this.tiles.push(<ISelectableTile>{
                id: `tile_${i}`,
                iconClass: 'icon',
                title: `Item ${i}`,
                value: `value ${i}`,
                enabled: true,
                selected: false,
                tooltip: `tooltip ${i}`,
                description: `Description ${1}`
            });
        }
    }

    onSelectionChanged(change: any): void {
        this.changeMessage = `Was '${change.previous?.value}', now '${change.current.value}'`;
    }

}
