import { Component, Inject, OnInit } from '@angular/core';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { ComplexOperator } from 'src/app/shared/enums/rules/complex-operator';
import { RulePluginService } from '../../../services/ruleplugin/rule-plugin-service';

@Component({
    selector: 'k2-group-boolean-operator-expression-harness',
    templateUrl: './group-boolean-operator-expression-harness.component.html',
    styleUrls: ['./group-boolean-operator-expression-harness.component.less']
})
export class GroupBooleanOperatorExpressionHarnessComponent implements OnInit {

    public k2Model = {
        expressions: [{}]
    };
    public sortable = false;
    public alternateColour = false;
    public nested = false;

    public selectedKey = 'key';
    public title = 'Selected Title';

    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;

    public dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    public itm: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredGroupOperator = {
        expressionOperator: ComplexOperator.And,
        directive: 'k2-group-boolean-operator-expression',
        listen: (()=>{}) as any,
        getParentModel: (() => ({
            indexOf: () => 0,
            splice: (i: number, len: number, expression: any): void => {}
        })) as any,
    };

    constructor(
        private _rulePluginService: RulePluginService
    ) {
        _rulePluginService.setDesignerContext('ruleDesignerHarness', <sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredRule>{}, ()=>{});
    }

    ngOnInit(): void {
    }

    onDrop() {
        console.log('onDrop');
    }

    onDropTop() {
        console.log('onDrop');
    }

    onDropBottom(){
        console.log('drop bottom');
    }

    onGroupOptionSelected(index){
        console.log('on group option selected - groupConditions');
    }
}
