import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ChooseControlHarnessComponent } from './choose-control-harness/choose-control-harness.component';
import { DevHarnessComponent } from './dev-harness.component';
import { DropDownHarnessComponent } from './drop-down-harness/drop-down-harness.component';
import { FilterControlHarnessComponent } from './filter-control-harness/filter-control-harness.component';
import { ListItemHarnessComponent } from './list-item-harness/list-item-harness.component';
import { ResourceServiceHarnessComponent } from './resource-service-harness/resource-service-harness.component';
import { FilterExpressionHarnessComponent } from './rules-designer/expressions/filter-expression-harness/filter-expression-harness.component';
import { GroupBooleanOperatorExpressionHarnessComponent } from './rules-designer/group-boolean-operator-expression-harness/group-boolean-operator-expression-harness.component';
import { ThisResponseExpressionHarnessComponent } from './rules-designer/this-response-expression-harness/this-response-expression-harness.component';
import { ScrollbarHarnessComponent } from './scrollbar-harness/scrollbar-harness.component';
import { TextBoxHarnessComponent } from './text-box-harness/text-box-harness.component';
import { CreateReferencesHarnessComponent } from './wizard-popup/sharepoint/create-references-harness/create-references-harness.component';
import { SharePointSummaryHarnessComponent } from './wizard-popup/sharepoint/summary-harness/summary-harness.component';
import { TileSelectHarnessComponent } from './tile-select-harness/tile-select-harness.component';
import { WizardPopupHostHarnessComponent } from './wizard-popup/wizard-popup-host-harness/wizard-popup-host-harness.component';
import { ProcessSettingsTabHarnessComponent } from './config-panel/plugins/plugin-wizards/logic-steps/start-step/process-settings-tab-harness/process-settings-tab-harness.component';

const routes: Routes = [
    {
        path: 'dd',
        component: DropDownHarnessComponent
    },
    {
        path: 'scroll',
        component: ScrollbarHarnessComponent
    },
    {
        path: 'gbo',
        component: GroupBooleanOperatorExpressionHarnessComponent
    },
    {
        path: 'textbox',
        component: TextBoxHarnessComponent
    },
    {
        path: 'choose',
        component: ChooseControlHarnessComponent
    },
    {
        path: '',
        component: DevHarnessComponent
    },
    {
        path: 'filter',
        component: FilterControlHarnessComponent
    },
    {
        path: 'thisresponse',
        component: ThisResponseExpressionHarnessComponent
    },
    {
        path: 'resourceservice',
        component: ResourceServiceHarnessComponent
    },
    {
        path: 'filterexpression',
        component: FilterExpressionHarnessComponent
    },
    {
        path: 'listitem',
        component: ListItemHarnessComponent
    },
    {
        path: 'create-references',
        component: CreateReferencesHarnessComponent
    },
    {
        path: 'sp-summary',
        component: SharePointSummaryHarnessComponent
    },
    {
        path: 'tileselect',
        component: TileSelectHarnessComponent
    },
    {
        path: 'wizardpopup',
        component: WizardPopupHostHarnessComponent
    },
    {
        path: 'processsettings',
        component: ProcessSettingsTabHarnessComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DevHarnessRoutingModule { }
