import { Component, EventEmitter, Input, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { PageComponent } from 'src/app/components/wizard-popup/page/page.component';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';

@Component({
    selector: 'k2-page-one',
    templateUrl: './page-one.component.html',
    styleUrls: ['./page-one.component.less'],
    providers: [{ provide: PageComponent, useExisting: PageOneComponent }]
})
export class PageOneComponent extends PageComponent implements OnInit {

    @Input() isPageTwoIncluded = false;
    @Output() isPageTwoIncludedChange = new EventEmitter<boolean>();

    buttonConfig: IPopupButtonsConfig;

    beforePageDisplayed() {
        this.buttonConfig = {
            next: {
                isDisabled: true
            }
        };
    }

    beforeNavigateNext() {
    }

    beforeNavigatePrevious() {}

    constructor(protected resourceService: ResourceService) {
        super(resourceService);
        console.log('page one ctor');
    }

    ngOnInit(): void {
        console.log('page one init');
    }

    changeSomething(){
        this.buttonConfig.next.isDisabled = false;
        this.isPageTwoIncluded = true;
        this.isPageTwoIncludedChange.emit(this.isPageTwoIncluded);
        this.notifyConfigChanged();
    }

}
