import { Component, OnInit } from '@angular/core';
import { ResourceService } from 'src/app/services/resource/resource.service';

@Component({
    selector: 'k2-resource-service',
    templateUrl: './resource-service-harness.component.html',
    styleUrls: ['./resource-service-harness.component.less']
})
export class ResourceServiceHarnessComponent implements OnInit {

    textOne = '';
    textTwo = '';
    textThree = '';

    constructor(private _resourceService: ResourceService) {

    }

    ngOnInit(): void {
    }

    populateTextOne(){
        this._resourceService.getFromResx('resourceOne', 'keyOne').subscribe(msg => {
            this.textOne = msg;
        });
    }

    populateTextTwo(){
        this._resourceService.getFromResx('resourceOne', 'keyOne').subscribe(msg => {
            this.textTwo = msg;
        });
    }

    populateTextThree(){
        this._resourceService.getFromResx('resourceOne', 'keyOne').subscribe(msg => {
            this.textThree = msg;
        });
    }



}
